import React from "react";

import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";

import "./HireMinersDialog.scss";

class HireMinersDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = { minersCount: 0, bnbAmount: "" };

    this.render = this.render.bind(this);
    this.inputBnbAmount = this.inputBnbAmount.bind(this);
  }

  inputBnbAmount(event) {
    const raw = event.target.value;
    const value = Number(raw);

    if (raw === "") {
      this.setState({ bnbAmount: raw });
      return;
    }

    if (value < 0 || value !== parseInt(raw, 10)) {
      return;
    }

    this.setState({ bnbAmount: String(value) });
  }

  render() {
    const { nativeBalance } = this.props;
    const { minersCount, bnbAmount } = this.state;
    return (
      <Col>
        <Row className="align-items-center">
          <Col>
            <Row>
              <Col xs="auto">
                Enter BNB Amount
              </Col>
            </Row>
            <Row className="position-relative align-items-center">
              <Form.Control value={bnbAmount} onChange={this.inputBnbAmount} className="bg-transparent border border-dark rounded HireMinersDialog__input" />
              <Badge className="fs-6 position-absolute bg-transparent text-dark HireMinersDialog__badge">BNB</Badge>
            </Row>
            <Row>
              <Col xs="auto">
                Your Balance:&nbsp;
                {nativeBalance} BNB
              </Col>
            </Row>
          </Col>
          <div className="w-100 d-md-none" />
          <Col xs="12" md="auto" className="ps-4 mt-2 mt-md-0">
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button">
              Hire&nbsp;
              {minersCount}
              &nbsp;Miners
            </Button>
          </Col>
        </Row>
        <Row className="mt-4">
          <Col>
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button w-100">
              Compound
            </Button>
          </Col>
          <Col>
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button w-100">
              Pocket Your BNB
            </Button>
          </Col>
        </Row>
      </Col>
    );
  }
}

export default HireMinersDialog;
