import React from "react";
import PropTypes from "prop-types";

import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

import {message} from 'antd';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import "./RefererLink.scss";

class RefererLink extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const {
      xs, md, lg, xl, xxl, address
    } = this.props;
    const refLink = `https://superminer.app/#/?ref=${address}`;
    return (
      <Col xs={xs} md={md} lg={lg} xl={xl} xxl={xxl}>
        <Row>
          <Col xs="12" lg="auto" className="ms-lg-4 fs-5 text-uppercase fw-bold">
            Referral Link
          </Col>
        </Row>
        <Row className="align-items-center mt-2">
          <Col className="RefererLink__input">
            {refLink}
          </Col>
          <div className="d-lg-none" />
          <Col xs="12" lg="auto" className="mt-2 mt-lg-0">

          <CopyToClipboard text={refLink}
            onCopy={() => {message.info("Copied to clipboard")}}>
            <Button className="rounded px-4 border border-dark text-dark fw-bold RefererLink__copyButton">
              Copy
            </Button>
          </CopyToClipboard>


          </Col>
        </Row>
      </Col>
    );
  }
}

RefererLink.propTypes = {
  xs: PropTypes.string,
  md: PropTypes.string,
  lg: PropTypes.string,
  xl: PropTypes.string,
  xxl: PropTypes.string,
};

RefererLink.defaultProps = {
  xs: "",
  md: "",
  lg: "",
  xl: "",
  xxl: "",
};

export default RefererLink;
