import React from "react";

import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

class BalanceState extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.render = this.render.bind(this);
  }

  render() {
    const { contractBalance, totalMiners } = this.props;

    return (
      <Col className={`d-flex flex-column fs-4`}>
        <Row>
          <Col xs="8">
            <Row>
              Contract Balance:&nbsp;
            </Row>
            <Row>
              Total Miners:&nbsp;
            </Row>
          </Col>
          <Col className="fw-bold">
            <Row>
              {contractBalance}
            </Row>
            <Row>
              {totalMiners}
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}
export default BalanceState;
