import React, {Component} from 'react';
import {Switch, Route} from "react-router-dom";
import Home from "./views/Home/Home";
import Faq from "./views/Faq/Faq";
import {
  Utils,
} from './utils/utils';
class Routes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nativeBalance: 0,
      contractBalance: 0,
      totalMiners:0
    }
  }
  async componentDidMount() {
    this.checkContract()
    this.timer = setInterval(()=>{this.checkContract()}, 1000);
  }

checkContract=()=>{
  if(Utils.web3 &&
    this.props.address){
      clearInterval(this.timer);
      this.getNativeBalance();
    }
  }

  getNativeBalance=()=>{
    if(this.props.address){
      Utils.web3.eth.getBalance(this.props.address)
      .then( (res)=>{
        this.setState({
          nativeBalance: Utils.web3.utils.fromWei(res),
        })
      })
      .catch(console.log)
    }
  }

  render(){
    return(

        <Switch>
          <Route exact path="/" render={
            ()=>(
              <Home
                address={this.props.address}
                totalMiners={this.state.totalMiners}
                nativeBalance={this.state.nativeBalance}
                contractBalance={this.state.contractBalance}
              />
            )
          }/>
          <Route exact path="/faq" render={
            ()=>(
              <Faq address={this.props.address}/>
            )
          }/>

          <Route render={
            ()=>(
              <Home
                address={this.props.address}
                totalMiners={this.state.totalMiners}
                nativeBalance={this.state.nativeBalance}
                contractBalance={this.state.contractBalance}
              />
            )
          }/>
        </Switch>

    )
  }
}

export default Routes;
